<script>
import { mapActions } from 'vuex'
export default {
  emits: ['nextStep', 'prevStep'],
  data() {
    return {
      school: {
        "full_name": "",
        "short_name": "",
        "status_olympic": "",
        "mark_olympic": "",
        "grade_number": "",
        "grade_letter": "",
        "grade_performs": "",
        "fio_teacher": "",
        "place_work": "",
        "post": "",
        "academic_degree": "",
        statusOptions: [
          {
            label: '    2022 ',
            value: '1'
          },
          {
            label: '     2022 ',
            value: '2'
          },
          {
            label: '    2021 ',
            value: '3'
          },
        ],
        classOptions: [
          {
            label: '9',
            value: '9'
          },
          {
            label: '10',
            value: '10'
          },
          {
            label: '11',
            value: '11'
          },
        ],
        letterOptions: [
          { label: '-', value: '-' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' },

        ],
        degreeOptions: [
          {
            label: ' ',
            value: '1'
          },
          {
            label: ' ',
            value: '2'
          },
          {
            label: ' ',
            value: '3'
          },
        ],
      },
      rules: {
        fio_teacher: [
          {
            required: true,
            message: ' ',
            trigger: 'blur',
          }
        ],
        place_work: [
          {
            required: true,
            message: ' ',
            trigger: 'blur',
          }
        ],
        post: [
          {
            required: true,
            message: ' ',
            trigger: 'blur',
          }
        ],
        academic_degree: [
          {
            required: true,
            message: ' ',
            trigger: 'blur',
          }
        ],
        full_name: [
          {
            required: true,
            message: '  ',
            trigger: 'blur',
          }
        ],
        short_name: [
          {
            required: true,
            message: '  ',
            trigger: 'blur',
          }
        ],
        status_olympic: [
          {
            required: true,
            message: ' ',
            trigger: 'blur',
          }
        ],
        grade_number: [
          {
            required: true,
            validator: this.gradeNumLet,
            trigger: 'blur',
          }
        ],
        grade_letter: [
          {
            required: true,
            message: ' ',
            trigger: 'blur',
          }
        ],
        grade_performs: [
          {
            required: true,
            message: ' ',
            trigger: 'blur',
          }
        ],
        mark_olympic: [
          {
            required: true,
            message: '  '
          },
          {
            type: 'number',
            message: '  '
          },
        ]
      }
    }
  },
  methods: {
    ...mapActions([
      'SET_SCHOOL']),
    gradeNumLet(rule, value, callback) {
      if (this.school.grade_number === '' || this.school.grade_letter === '') {
          callback(new Error(' '))
        } else {
          callback()
        }
    },
    onSubmit(formEl) {

      const data = {
        school: {
          full_name: this.school.full_name,
          short_name: this.school.short_name,
          status_olympic: this.school.status_olympic,
          mark_olympic: this.school.mark_olympic.toString(),
          grade_number: this.school.grade_number,
          grade_letter: this.school.grade_letter,
          grade_performs: this.school.grade_performs,
          fio_teacher: this.school.fio_teacher,
          place_work: this.school.place_work,
          post: this.school.post,
          academic_degree: this.school.academic_degree
        },
      }

      if (!formEl) return
       formEl.validate((valid) => {
      if (valid) {
        this.SET_SCHOOL(data);
        this.$emit('nextStep');
      } else {
        return false
    }
  })
    }
  }

